<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-21 10:20:26
 * @LastEditTime: 2019-08-28 09:25:56
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="market_wrapper">
      <Header></Header>
      <section class="marke_content">
        <el-row class="swipper_wrapper">
          <el-col class="swipper_content" >
            <el-carousel :interval="5000">
              <el-carousel-item v-for="item in imgs" :key="item.index">
                <el-image :src="item.imgSrc" fit="cover" @click.native="jump" ></el-image>
              </el-carousel-item>
            </el-carousel>
          </el-col>
        </el-row>

        <el-row class="notice_wrapper">
          <el-col class="notice_content" :span="16" :offset="4" >
            <el-carousel :interval="8000" arrow="never" direction="vertical">
              <el-carousel-item v-for="item in notices" :key="item.index">
                <el-row class="sigle_notice_row" @click.native="noticeDetail">
                  <el-col :span="12" class="sigle_notice_col">
                    <i class="iconfont icon-notice"></i>
                    {{item.title}}
                  </el-col>
                  <el-col :span="12" class="sigle_notice_col notice_time">
                    {{item.time}}
                  </el-col>
                </el-row>
              </el-carousel-item>
            </el-carousel>
          </el-col>
        </el-row>

        <el-row class="new_trade">
          <el-col class="title"> 创新型交易所，近24小时成交量<span>647370458</span>美元</el-col>
          <el-col class="advantage" :span="16" :offset="4">
            <div class="share ">
              <p class="imgContent"><img src="@/assets/img/share.png" alt=""></p>
              <p class="main_title">最适宜量化交易的平台</p>
              <p class="sm_title">流畅的API 交易体验，让您的自动化交易之旅更加完美</p>
            </div>
            <div class="air ">
              <p class="imgContent"><img src="@/assets/img/rocket.png" alt=""></p>
              <p class="main_title">百万级撮合效率</p>
              <p class="sm_title">撮合引擎能1秒能处理百万条订单</p>

            </div>
            <div class="money ">
              <p class="imgContent"><img src="@/assets/img/money.png" alt=""></p>
              <p class="main_title">挂单送手续费</p>
              <p class="sm_title">额外赠送手续费</p>
            </div>
          </el-col>
        </el-row>

        <el-row class="mul_wrapper">
          <el-col class="mul_title"> 随时随地 多平台终端交易</el-col>
          <el-col class="mul_sm_title">覆盖iOS、Android、Web多个平台，支持全业务功能</el-col>
          <el-col class="mul_detail_wrapper" :span="16" :offset="4">
            <div class="mul_detail_img">
              <img src="@/assets/img/mac.png" alt="">
            </div>
            <div class="mul_detail_down">
              <p class="down_wrapper" @mouseenter="onMouseOver('1')" @mouseleave="onMouseOut('1')">
                <a class="icon_bg fl">
                  <img class="phone_img" v-if="iphoneShow == 1" src="@/assets/img/iPhone.png" alt="">
                  <img class="phone_img" v-if="iphoneShow == 2" src="@/assets/img/down.png" alt="">
                  </a>
                <a class="down_word fl"> iPhone 下载 </a>
              </p>
              <!-- <p class="down_wrapper" @mouseenter="onMouseOver('2')" @mouseleave="onMouseOut('2')">
                <a class="icon_bg fl">
                  <img class="phone_img" v-if="anShow == 1" src="@/assets/img/andio.png" alt="">
                  <img class="phone_img" v-if="anShow == 2" src="@/assets/img/down.png" alt="">
                  </a>
                <a class="down_word fl"> Andriod 下载 </a>
              </p> -->
              <p class="code_wrapper">
                <img src="@/assets/img/code.png" alt="">
              </p>
              <!-- <p class="code_word">IOS & Android 扫码下载 </p> -->
              <p class="code_word">IOS 扫码下载 </p>

            </div>
          </el-col>
        </el-row>

        <el-row class="mul_resgist">
          <el-col class="mul_res_title"> 马上注册 开启财富之旅</el-col>
          <el-col class="mul_res_input">
            <el-input class="res_input_wrapper" placeholder="输入您的邮箱号" v-model="registInput">
              <el-button slot="append" @click.native="goTo">注册</el-button>
            </el-input>
          </el-col>
        </el-row>

      </section>
      <user-foot></user-foot>
  </div>
</template>

<script>
  import Header from '@/components/header/header'
  import UserFoot from '@/components/userFoot/userFoot'

  export default {
    components: {Header,UserFoot},
    props: {},
    data() {
      return {
        imgs:[{imgSrc:'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg'},{imgSrc:'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg'}],
        notices:[{title:'公告1',time:'2019-08-16'},{title:'公告2',time:'2019-08-17'},{title:'公告3',time:'2019-08-18'}],
        iphoneShow:'1',
        anShow:'1',
        registInput:''
      }
    },
    watch: {},
    computed: {},
    methods: {
      /* 首页跳转 */
      jump(){
        this.$router.replace('/home/trade')
      },

      /* 注册跳转 */
      goTo(){
        this.$router.push({path:'/register',query:{account:this.registInput}})
      },

      /* 公告详情 */
      noticeDetail(){
        // const meta = {name:'首页', url:'/'}
        // localStorage.setItem('breads',JSON.stringify(meta))
        // this.$router.push({path:'/noticeDetail',query:{name:'详情'}})
        this.$message.info('暂未开放，敬请期待')
      },

      /* 下载移入移出事件 */
      onMouseOver(str){
        switch(str){
          case '1':
            this.iphoneShow = 2
            break;
          case '2':
            this.anShow = 2
            break;
          default:
            break;
        }
      },
      onMouseOut(str){
        switch(str){
          case '1':
            this.iphoneShow = 1
            break;
          case '2':
            this.anShow = 1
            break;
          default:
            break;
        }
      }
    },
    created() {},
    mounted() {}
  };
</script>
<style lang="scss" scoped>
  @import '@/style/base.scss';
  .market_wrapper{
    height: 100%;
    width: 100%;
    min-width: 1280px;
    padding-top: 47px;
    box-sizing: border-box;
    .marke_content{
      width: 100%;
      min-height: 100%;
      .swipper_wrapper{
        height: 500px;
        .swipper_content{
          height: 100%;
          cursor: pointer;
        }
      }
      .notice_wrapper{
        height: 40px;
        background: $notice_bg;
        width: 100%;
        .notice_content{
          height: 100%;
          .sigle_notice_row{
            height: 100%;
            cursor: pointer;
            .sigle_notice_col{
              height: 100%;
              line-height: 40px;
              font-size: $base_font;
              color: $light_title_color;
              i{
                margin-right: 5px;
                color: $active_font_color;
              }
            }
            .notice_time{
              color: $dark_title_color;
              text-align: right;
            }
          }
        }
        
      }
      .new_trade{
        width: 100%;
        height: 340px;
        background-color: $light_font_color;
        padding: 35px 0 50px;
        box-sizing: border-box;
        .title{
          line-height: 30px;
          height: 30px;
          color: $slider_dark_bg_color;
          font-size: $font24;
          font-weight: 600;
          text-align: center;
          margin-bottom: 25px;
          span{
            color: $active_font_color;
          }
        }
        .advantage{
          display: flex;
          justify-content: space-around;
          >div{
            min-width:160px;
            max-width: 200px;
            >p{
              width: 100%;
              text-align: center;
            }
          }
          .imgContent{
            margin: 0 auto;
            width: 121px;
            height: 136px;
            img{
              width: 100%;
              height: 100%;
            }
          }
          .main_title{
            text-align: center;
            font-size: $font16;
            color: $slider_dark_bg_color;
            margin-bottom: 10px;
          }
          .sm_title{
            text-align: center;
            font-size: $base_font;
            color: $table_thead_font_color;
          }
        }
      }
      .mul_wrapper{
        width: 100%;
        min-height: 480px;
        background-image: url('~@/../static/ohterBg.jpg');
        background-repeat: no-repeat;
        background-size: 100% 100%;
        padding: 35px 0 50px;
        .mul_title{
          line-height: 30px;
          height: 30px;
          color: $light_font_color;
          font-size: $font24;
          font-weight: 600;
          text-align: center;
          margin-bottom: 10px;
        }
        .mul_sm_title{
          line-height: 20px;
          height: 20px;
          color: $dark_font_color;
          font-size: $font14;
          text-align: center;
          margin-bottom: 25px;
        }
        .mul_detail_wrapper{
          display: flex;
          justify-content: space-between;
          .mul_detail_img{
            width: 50%;
            height: 100%;
            text-align: center;
            img{
              width: 495px;
              height: 354px;
            }
          }
          .mul_detail_down{
            width: 50%;
            height: 100%;
            padding-left: 150px;
            box-sizing: border-box;
            .icon_bg{
              width: 36px;
              height: 36px;
              border-radius: 50%;
              background-color: $light_font_color;
              text-align: center;
              margin-top: 14px;
              margin-left: 10px;
              .phone_img{
                width: 20px;
                height: 22px;
                margin-top: 6px;
              }
            }
            .down_word{
              color: $light_font_color;
              margin-left: 10px;
              font-size: $base_font;
              line-height: 64px;
            }
            .down_wrapper{
              width: 180px;
              height: 64px;
              border: 1px solid transparent;
              &:hover{
                border-color: $border_active_color;
                background-color: $hover_bg;
              }
            }
            .code_wrapper{
              width: 180px;
              text-align: center;
              margin-top: 30px;
              img{
                width: 141px;
                height: 140px;
              }
            }
            .code_word{
              margin-top: 5px;
              color: $light_font_color;
              font-size: $base_font;
              width: 180px;
              text-align: center;
            }

          }

        }
      }
      .mul_resgist{
        width: 100%;
        height: 200px;
        background-color: $light_font_color;
        padding: 35px 0 50px;
        .mul_res_title{
          width: 100%;
          text-align: center;
          font-size: $font24;
          color: $slider_dark_bg_color;
          font-weight: 600;
          margin-bottom: 25px;
        }
        .mul_res_input{
          text-align: center;
          .res_input_wrapper{
            width: 500px;
          }
        }
      }
      
    }
  }
</style>


